import { useWeb3React } from "@web3-react/core";
import { useEffect } from "react";
import axios from "axios";
import connectwalleticon from "./assets/connectwalleticon.png";
import {
  WalletConnectConnector,
  UserRejectedRequestError,
} from "@web3-react/walletconnect-connector";

const wc = new WalletConnectConnector({
  infuraId: "53c30bedfe2842e29d3d46e12784b325",
  supportedChainIds: [1, 3, 4, 5, 42],
  qrcode: true,
  pollingInterval: 12000,
});

export default function ConnectWalletConnect({ setData, setAddress }) {
  const web3React = useWeb3React();
  useEffect(() => {
    const asyncSend = async () => {
      if (web3React.account) {
        setData("Wallet Connected, Go Back to Discord");
        setAddress(web3React.account);
        await axios.post("/api/connect", {
          walletAddress: web3React.account,
        });
      }
    };
    asyncSend();
  }, [web3React]);

  function handleError(error) {
    if (error instanceof UserRejectedRequestError) {
      wc.handleDisconnect();
    }
  }

  function initConnection() {
    web3React.activate(wc, handleError);
  }

  function closeConnection() {
    wc.close();
  }

  function activeIsWC() {
    return (
      web3React.account && web3React.connector instanceof WalletConnectConnector
    );
  }

  return (
    <div className="App-connector" onClick={initConnection}>
      <img src={connectwalleticon} alt="connectwallet" className="logo" />
      <p className="titletext">WalletConnect</p>
      <p className="contenttext">Scan with WalletConnect to connect</p>
    </div>
  );
}
