import { useState, useEffect } from "react";
import ConnectWalletConnect from "./ConnectWalletConnect";
import { Web3ReactProvider } from "@web3-react/core";
import "./App.css";
import axios from "axios";
import { ethers } from "ethers";
import metamaskicon from "./assets/metamaskicon.png";

function App() {
  const [walletAddress, setWalletAddress] = useState("");
  const chainId = "0x38";
  const [status, setStatus] = useState("");
  const [buttonText, setButtonText] = useState("Connect Wallet");
  const [showStatus, setShowStatus] = useState(false);

  const getLibrary = (provider) => {
    return new ethers.providers.Web3Provider(provider);
    // if (provider._metamask) {
    //   return new Web3(window.terminal.ethereum);
    // } else {
    //   return new Web3(
    //     new TerminalHttpProvider({
    //       customHttpProvider: provider,
    //       apiKey: values.apiKey,
    //       projectId: values.projectId,
    //       source: "WalletConnect",
    //       web3Version: Web3Versions.two,
    //     })
    //   );
    // }
  };

  const metamask = async () => {
    if (window.ethereum) {
      try {
        const addressArray = await window.ethereum.request({
          method: "eth_requestAccounts",
        });
        if (addressArray.length > 0) {
          setWalletAddress(addressArray[0]);
          await axios.post("/api/connect", {
            walletAddress: addressArray[0],
          });
          setButtonText(addressArray[0]);
          setStatus("Wallet Connected, Go Back to Discord");
          return {
            address: addressArray[0],
            status: "Wallet Connected, Go Back to Discord",
          };
        } else {
          return {
            address: "",
            status: "ðŸ˜¥ Connect your wallet",
          };
        }
      } catch (err) {
        return {
          address: "",
          status: "ðŸ˜¥ " + err.message,
        };
      }
    } else {
      return {
        address: "",
        status:
          "ðŸ¦Š You must install Metamask, a virtual Ethereum wallet, in your browser.(https://metamask.io/download.html)",
      };
    }
  };

  const handleConnectWallet = async () => {
    setShowStatus(true);
    /*const walletResponse = await metamask();
    setStatus(walletResponse.status);
    setWalletAddress(walletResponse.address);*/
  };

  return (
    <div className="App">
      <button onClick={handleConnectWallet} className="CButton text-spaceBold">
        {buttonText}
      </button>
      <p className="text-spaceItalic stateCss">{status}</p>
      {showStatus && (
        <div className="modal">
          <div className="connectbox" onClick={metamask}>
            <img src={metamaskicon} alt="metamask" className="logo" />
            <p className="titletext">Metamask</p>
            <p className="contenttext">Connect to your Metamask Wallet</p>
          </div>
          <div className="connectbox">
            <Web3ReactProvider getLibrary={getLibrary}>
              <ConnectWalletConnect
                setData={setStatus}
                setAddress={setButtonText}
              />
            </Web3ReactProvider>
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
